import React, { JSX } from 'react';
import cn from 'classnames';
import { addDomainNameToPath } from '5_entities/utils/addDomainNameToPath';

interface ServerImgProps {
    src: string;
}

export function ServerImg( { src }: ServerImgProps ): JSX.Element {
    return (
        <img src={addDomainNameToPath(src)} alt="" />
    );
}
